/* header */
.header {
  display: flex;
}

.header__top {
  max-width: 1600px;
  width: 100%;
  height: 120px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-left: auto;
  margin-right: auto;
}

.header__top-contacts {
  color: #000;
}

.header__top-contacts a {
  display: flex;
  align-items: center;
  margin-bottom: 3px;
}

.header__top-contacts span {
  color: #000;
  font-size: 11px;
  display: flex;
  align-items: center;
  justify-content: flex-end;
}

.header__top-menu {
  color: #FFF;
  display: flex;
  align-items: center;
}

/* icons */
.phoneIcon {
  width: 30px;
  height: 30px;
}

.socialIcon {
  width: 17px;
  height: 16px;
}

.whatsApp {
  margin-left: 5px;
  margin-right: 10px;
}

.menuIcon {
  width: 29px;
  height: 19px;
  margin-left: 7px;
  cursor: pointer;
}

